import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { PostItComponent } from '../post-it/post-it.component';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../data.service';
import { PostIt } from '../postIt';

@Component({
  selector: 'app-driehoek1',
  templateUrl: './driehoek1.component.html',
  styleUrls: ['./driehoek1.component.css']
})
export class Driehoek1Component implements OnInit {

  postItsTop: PostIt[];
  postItsMiddle: PostIt[];
  postItsBottom: PostIt[] = [];
  school: string;

  constructor(private dialog: MatDialog, private route: ActivatedRoute, private dataService: DataService) {
    this.school = this.route.snapshot.paramMap.get("school");
    this.school = this.school.replace("_", " ");
    this.dataService.getDataDriehoek1(this.school).map((data:any) => {
      this.postItsBottom.push(data);
    });
    console.log(this.postItsBottom);
  }

  ngOnInit() {
  }

  showPostIt(postIt) {
    const dialogRef = this.dialog.open(PostItComponent, {
      width: '75%',
      height: '75%',
      data: postIt
    });
    dialogRef.afterClosed().subscribe((res: any) => {
    });
  }
}
